@import url(https://fonts.bunny.net/css?family=fira-code:300,400,500,600,700|fira-sans:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i|inter:900);

:root {
  --primary: #A84FFF;
  --secondary: #D81E5B;
  --dark: #131A26;
  --light: #EEEEEE;
  --grey: #888888;

  --font: 'Inter', sans-serif;
  --font-quote: 'Fira Code', monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font);
  background-color: var(--dark);
  margin: 0;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.quote {
  position: relative;
}

.anime,
.character {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  color: var(--light);
  border-radius: 1.5rem;
  font-size: 1.4rem;
  letter-spacing: 1px;
  max-width: 360px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

blockquote {
  font-family: var(--font-quote);
  max-width: 720px;
  font-size: 2rem;
  background-color: var(--dark);
  padding: 3rem;
  border-radius: 1.5rem;
  color: #fff;
}

.anime {
  background-color: var(--secondary);
  font-weight: 400;
  transform: translate(-25%, 50%);
}

.character {
  background-color: var(--primary);
  font-weight: 700;
  transform: translate(25%, 50%);
  position: absolute;
  right: 0;
  bottom: 0;
}

button {
  appearance: none;
  border: none;
  outline: none;
  background-color: var(--primary);
  color: #fff;
  border-radius: 3rem;
  padding: 0.75rem 1.5rem;
  margin: 64px 0;
  transition: 0.4s;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 700;
}

button:hover {
  background-color: var(--secondary);
}

.footer {
  background-color: var(--dark);
  color: #fff;
  text-align: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-size: 12px;
  font-weight: 200;
}

.header {
  top: 0;
  font-size: 12px;
  color: #fff;
}

h1 {
  color: #aaa;
  font-size: 24px;
  font-weight: 400;
}

strong {
  color: #313131;
}
